	module.exports = {"pathContext":{"frontmatter":{"path":"/docs/width-height","title":"Width and Height","hasPlayground":true,"initialPlayground":null,"redirect":null},"html":"<h2>Width and Height</h2>\n<p>The <code class=\"language-text\">width</code> property in Yoga specifies the width of the element's content area.\nSimilarly <code class=\"language-text\">height</code> property specifies the height of the element's content area.</p>\n<p>Both <code class=\"language-text\">width</code> and <code class=\"language-text\">height</code> can take following values:</p>\n<p><strong>AUTO</strong> Is the default Value, Yoga calculates the width/height for the element based\non its content, whether that is other children, text, or an image.</p>\n<p><strong>PIXELS</strong> Defines the width/height in absolute pixels. Depending on other properties set on\nthe Yoga node this may or may not be the final dimension of the node.</p>\n<p><strong>PERCENTAGE</strong> Defines the width or height in percentage of its parent's width or height respectively.</p>\n<h3>Width</h3>\n<p><controls prop=\"width\"></controls></p>\n<h3>Height</h3>\n<p><controls prop=\"height\"></controls></p>"}}

